import { createRouter, createWebHashHistory } from "vue-router"

import LandingPage from "../views/LandingPage.vue"
import UserLogin from "../views/UserLogin.vue"
import ProductList from "../components/ProductList.vue"
import Cart from "../components/ShoppingCart.vue"
import OrderHistory from "../views/OrderHistory.vue"
import CheckoutMonitor from "../components/CheckoutMonitor.vue"
import MonitorProducts from "../components/MonitorProducts.vue"
import AddProduct from "../components/AddProduct.vue"

const routes = [
  { path: "/", component: LandingPage },
  { path: "/login", component: UserLogin },
  // { path: "/register", component: UserRegister }, // Commented out for demo
  { path: "/products", component: ProductList },
  { path: "/cart", component: Cart },
  { path: "/orders", component: OrderHistory },
  { path: "/monitor", component: CheckoutMonitor },
  { path: "/monitor-products", component: MonitorProducts },
  { path: "/add-product", component: AddProduct },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
})

// 🔐 GLOBAL GUARD FOR ALL NAVIGATION
router.beforeEach((to, from, next) => {
  const isLoggedIn = !!localStorage.getItem("access")
  const isStaff = localStorage.getItem("is_staff") === "true"

  // const guestOnly = ["/login", "/register"] // Original
  const guestOnly = ["/login"] // Updated for demo - removed register
  const staffOnly = ["/monitor", "/monitor-products", "/add-product"]
  const customerOnly = ["/products", "/cart", "/orders"]
  const publicRoutes = ["/"] // Landing page is public and accessible to everyone

  // Allow access to public routes (landing page)
  if (publicRoutes.includes(to.path)) {
    return next()
  }

  // Not logged in → block access to protected routes, redirect to landing page
  if (!isLoggedIn && !guestOnly.includes(to.path) && !publicRoutes.includes(to.path)) {
    return next("/")
  }

  // Logged in → redirect from guest pages to appropriate dashboard
  if (isLoggedIn && guestOnly.includes(to.path)) {
    return next(isStaff ? "/monitor" : "/products")
  }

  // Staff cannot access customer pages
  if (isLoggedIn && isStaff && customerOnly.includes(to.path)) {
    return next("/monitor")
  }

  // Customer cannot access admin pages
  if (isLoggedIn && !isStaff && staffOnly.includes(to.path)) {
    return next("/products")
  }

  next()
})

export default router
