<template>
  <div class="login-container">
    <div class="login-card">
      <div class="login-header">
        <div class="login-icon">
          <i class="bi bi-bag-heart-fill"></i>
        </div>
        <h2>Welcome Back</h2>
        <p>Sign in to your account to continue</p>
      </div>
      
      <!-- Demo Credentials - Moved above the form -->
      <div class="demo-credentials" v-if="isDemoMode">
        <div class="demo-credentials-header">
          <i class="bi bi-info-circle me-2"></i>
          Demo Credentials
        </div>
        <div class="demo-credentials-body">
          <div class="demo-credential-item">
            <span class="demo-credential-label">Admin:</span>
            <span class="demo-credential-value">username: <strong>admin</strong>, password: <strong>admin</strong></span>
          </div>
          <div class="demo-credential-item">
            <span class="demo-credential-label">Customer:</span>
            <span class="demo-credential-value">username: <strong>user</strong>, password: <strong>user</strong></span>
          </div>
        </div>
      </div>
      
      <form @submit.prevent="handleLogin" class="login-form" novalidate>
        <div class="form-group">
          <label for="username">Username</label>
          <div class="input-group" :class="{ 'has-error': hasLoginError }">
            <span class="input-group-text" :class="{ 'error': hasLoginError }">
              <i class="bi bi-person"></i>
            </span>
            <input 
              type="text" 
              id="username" 
              v-model="username" 
              class="form-control" 
              :class="{ 'is-invalid': hasLoginError }"
              placeholder="Enter your username" 
              required
            />
          </div>
        </div>
        
        <div class="form-group">
          <label for="password">Password</label>
          <div class="input-group" :class="{ 'has-error': hasLoginError }">
            <span class="input-group-text" :class="{ 'error': hasLoginError }">
              <i class="bi bi-lock"></i>
            </span>
            <input 
              :type="showPassword ? 'text' : 'password'" 
              id="password" 
              v-model="password" 
              class="form-control" 
              :class="{ 'is-invalid': hasLoginError }"
              placeholder="Enter your password" 
              required
            />
            <button 
              type="button" 
              class="btn btn-outline-secondary toggle-password" 
              :class="{ 'error': hasLoginError }"
              @click="togglePasswordVisibility"
            >
              <i :class="showPassword ? 'bi bi-eye-slash' : 'bi bi-eye'"></i>
            </button>
          </div>
          
          <!-- Compact error message below password field -->
          <div v-if="errorMessage" class="error-message">
            <i class="bi bi-exclamation-circle me-1"></i>
            {{ errorMessage }}
          </div>
        </div>
        
        <div class="form-check mb-3">
          <input type="checkbox" class="form-check-input" id="rememberMe" v-model="rememberMe">
          <label class="form-check-label" for="rememberMe">Remember me</label>
        </div>
        
        <button 
          type="submit" 
          class="btn btn-primary login-btn" 
          :disabled="isLoading"
        >
          <span v-if="isLoading" class="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
          {{ isLoading ? 'Signing In...' : 'Sign In' }}
        </button>
      </form>
      
      <div class="login-footer">
        <p>Don't have an account? 
          <router-link to="/register" class="auth-link">Create Account</router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, watch, nextTick } from 'vue'
import { useRouter } from 'vue-router'
import { auth } from '../auth'
import { useToast } from '../composables/useToast'

export default {
  setup() {
    const router = useRouter()
    const { showToast } = useToast()
    
    const username = ref('')
    const password = ref('')
    const rememberMe = ref(false)
    const showPassword = ref(false)
    const isLoading = ref(false)
    const errorMessage = ref('')
    
    // Computed property to check if there's a login error
    const hasLoginError = computed(() => {
      return !!errorMessage.value
    })
    
    // Check if we're in demo mode
    const isDemoMode = computed(() => {
      return window.location.hostname.includes('github.io') || 
             process.env.VUE_APP_DEMO_MODE === 'true'
    })
    
    // Clear error when user starts typing
    watch([username, password], () => {
      if (errorMessage.value) {
        errorMessage.value = ''
      }
    })
    
    const togglePasswordVisibility = () => {
      showPassword.value = !showPassword.value
    }
    
    const handleLogin = async () => {
      try {
        isLoading.value = true
        errorMessage.value = ''
        
        console.log('🔐 Starting login process...')
        
        // Perform login
        await auth.login(username.value, password.value)
        
        console.log('✅ Login successful, updating state...')
        
        // Force multiple state update events
        const authData = {
          isLoggedIn: true,
          username: username.value,
          isStaff: auth.isStaff,
          timestamp: Date.now()
        }
        
        // Dispatch multiple events to ensure one gets caught
        window.dispatchEvent(new CustomEvent('auth-changed', { detail: authData }))
        window.dispatchEvent(new CustomEvent('auth-state-changed', { detail: authData }))
        window.dispatchEvent(new CustomEvent('login-success', { detail: authData }))
        
        // Force a DOM update
        await nextTick()
        
        // Additional delay to ensure all components update
        await new Promise(resolve => setTimeout(resolve, 200))
        
        console.log('📢 Auth events dispatched, redirecting...')
        
        showToast({
          title: 'Login Successful',
          message: `Welcome back, ${username.value}!`,
          type: 'success'
        })
        
        // Redirect based on user role
        if (auth.isStaff) {
          router.push('/monitor')
        } else {
          router.push('/products')
        }
      } catch (error) {
        console.error('❌ Login error:', error)
        errorMessage.value = 'Invalid username or password'
        
        showToast({
          title: 'Login Failed',
          message: 'Invalid username or password',
          type: 'error'
        })
      } finally {
        isLoading.value = false
      }
    }
    
    return {
      username,
      password,
      rememberMe,
      showPassword,
      isLoading,
      errorMessage,
      hasLoginError,
      isDemoMode,
      togglePasswordVisibility,
      handleLogin
    }
  }
}
</script>

