<template>
  <div class="container">
    <div class="admin-header mb-4">
      <h2 class="section-title">
        <i class="bi bi-box-seam me-2"></i>Product Inventory
      </h2>
      <div class="admin-actions">
        <router-link to="/add-product" class="btn btn-add">
          <i class="bi bi-plus-circle me-2"></i>Add New Product
        </router-link>
      </div>
    </div>

    <!-- Stats Cards -->
    <div class="row mb-4">
      <div class="col-md-4">
        <div class="stat-card card">
          <div class="card-body">
            <div class="stat-icon">
              <i class="bi bi-box-seam"></i>
            </div>
            <div class="stat-info">
              <h3>{{ products.length }}</h3>
              <p>Total Products</p>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="stat-card card">
          <div class="card-body">
            <div class="stat-icon">
              <i class="bi bi-exclamation-triangle"></i>
            </div>
            <div class="stat-info">
              <h3>{{ lowStockCount }}</h3>
              <p>Low Stock Items</p>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="stat-card card">
          <div class="card-body">
            <div class="stat-icon">
              <i class="bi bi-cart-check"></i>
            </div>
            <div class="stat-info">
              <h3>{{ orders.length }}</h3>
              <p>Total Orders</p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Product Table -->
    <div class="card mb-4">
      <div class="card-header d-flex justify-content-between align-items-center flex-wrap gap-2">
        <h5 class="mb-0">Product Inventory</h5>
        <div class="d-flex align-items-center gap-2">
          <div class="search-container">
            <input 
              type="text" 
              class="form-control search-input" 
              placeholder="Search products..." 
              v-model="searchQuery"
              aria-label="Search products"
            />
            <i class="bi bi-search search-icon"></i>
          </div>
          <button class="btn btn-sm btn-outline-secondary" @click="refreshProducts" :disabled="isRefreshing">
            <span v-if="isRefreshing" class="spinner-border spinner-border-sm me-1" role="status"></span>
            <i v-else class="bi bi-arrow-clockwise me-1"></i>
            {{ isRefreshing ? 'Refreshing...' : 'Refresh' }}
          </button>
        </div>
      </div>
      
      <div v-if="isLoading" class="loading-container py-5">
        <div class="spinner">
          <div class="bounce1"></div>
          <div class="bounce2"></div>
          <div class="bounce3"></div>
        </div>
        <p>Loading products...</p>
      </div>
      
      <div v-else-if="filteredProducts.length === 0" class="empty-products py-5">
        <i class="bi bi-search-heart"></i>
        <h3>No products found</h3>
        <p>Try adjusting your search criteria.</p>
        <button class="btn btn-primary mt-3" @click="resetSearch">
          <i class="bi bi-arrow-counterclockwise me-2"></i>Reset Search
        </button>
      </div>
      
      <div v-else class="table-responsive">
        <table class="table table-hover">
          <thead>
            <tr>
              <th>Image</th>
              <th>Name</th>
              <th>Price</th>
              <th>Stock</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="product in paginatedProducts" :key="product.id">
              <td>
                <div class="product-image">
                  <img
                    v-if="product.image"
                    :src="fullImageUrl(product.image)"
                    :alt="product.name"
                    loading="lazy"
                  />
                  <div v-else class="image-placeholder">
                    <i class="bi bi-image"></i>
                  </div>
                </div>
              </td>
              <td>
                <div class="product-name">{{ product.name }}</div>
                <div class="product-description">{{ truncate(product.description, 50) }}</div>
              </td>
              <td>₱{{ formatPrice(product.price) }}</td>
              <td>
                <span 
                  class="stock-badge" 
                  :class="{
                    'bg-danger': product.stock === 0,
                    'bg-warning': product.stock > 0 && product.stock < 5,
                    'bg-success': product.stock >= 5
                  }"
                >
                  {{ product.stock }}
                </span>
              </td>
              <td>
                <div class="btn-group">
                  <button class="btn btn-sm btn-primary" @click="openEditModal(product)" aria-label="Edit product">
                    <i class="bi bi-pencil"></i>
                  </button>
                  <button class="btn btn-sm btn-danger" @click="confirmDeleteProduct(product.id)" aria-label="Delete product">
                    <i class="bi bi-trash"></i>
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      
      <!-- Pagination -->
      <div class="card-footer" v-if="totalPages > 1">
        <nav aria-label="Product pagination">
          <ul class="pagination justify-content-center mb-0">
            <li class="page-item" :class="{ disabled: currentPage === 1 }">
              <button class="page-link" @click="changePage(currentPage - 1)" :disabled="currentPage === 1" aria-label="Previous page">
                <i class="bi bi-chevron-left"></i>
              </button>
            </li>
            <li
              v-for="n in getPageNumbers()"
              :key="n"
              class="page-item"
              :class="{ active: currentPage === n }"
            >
              <button v-if="n === '...'" class="page-link" disabled>{{ n }}</button>
              <button v-else class="page-link" @click="changePage(n)">{{ n }}</button>
            </li>
            <li class="page-item" :class="{ disabled: currentPage === totalPages }">
              <button class="page-link" @click="changePage(currentPage + 1)" :disabled="currentPage === totalPages" aria-label="Next page">
                <i class="bi bi-chevron-right"></i>
              </button>
            </li>
          </ul>
        </nav>
      </div>
    </div>

    <!-- Recent Orders -->
    <div class="card">
      <div class="card-header">
        <h5 class="mb-0">Recent Orders</h5>
      </div>
      <div class="table-responsive">
        <table class="table">
          <thead>
            <tr>
              <th>Order ID</th>
              <th>Date</th>
              <th>Customer</th>
              <th>Items</th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="orders.length === 0">
              <td colspan="5" class="text-center py-4">No orders found</td>
            </tr>
            <tr v-for="order in recentOrders" :key="order.id">
              <td>#{{ order.id }}</td>
              <td>{{ formatDate(order.date_ordered) }}</td>
              <td>{{ order.customer }}</td>
              <td>{{ order.items ? order.items.length : 0 }} items</td>
              <td>
                <span class="badge bg-success">Completed</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- Edit Modal -->
    <div class="modal fade" ref="editModal" id="editModal" tabindex="-1" aria-labelledby="editModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered modal-lg">
        <div class="modal-content">
          <form @submit.prevent="validateAndSubmitEdit" novalidate>
            <div class="modal-header">
              <h5 class="modal-title" id="editModalLabel">
                <i class="bi bi-pencil-square me-2"></i>Edit Product
              </h5>
              <button type="button" class="btn-close" @click="closeEditModal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <!-- Scrollable content -->
              <div class="modal-body-scrollable">
                <!-- Product Image Preview -->
                <div class="text-center mb-4" v-if="editProduct.image">
                  <div class="product-image-preview">
                    <img 
                      :src="fullImageUrl(editProduct.image)" 
                      :alt="editProduct.name" 
                      class="img-fluid rounded" 
                    />
                  </div>
                  <p class="image-caption mt-2">Current image</p>
                </div>
                
                <div class="form-group mb-3">
                  <label class="form-label fw-medium">Product Name</label>
                  <input 
                    v-model="editProduct.name" 
                    class="form-control" 
                    :class="{'is-invalid': errors.name}"
                    required 
                    @blur="validateName"
                    placeholder="Enter product name"
                  />
                  <div class="invalid-feedback" v-if="errors.name">{{ errors.name }}</div>
                </div>
                
                <div class="form-group mb-3">
                  <label class="form-label fw-medium">Description</label>
                  <textarea 
                    v-model="editProduct.description" 
                    class="form-control" 
                    :class="{'is-invalid': errors.description}"
                    rows="3" 
                    required
                    @blur="validateDescription"
                    placeholder="Enter product description"
                  ></textarea>
                  <div class="invalid-feedback" v-if="errors.description">{{ errors.description }}</div>
                </div>
                
                <div class="row mb-3">
                  <div class="col-12 col-sm-6 mb-3 mb-sm-0">
                    <label class="form-label fw-medium">Price (₱)</label>
                    <div class="input-group">
                      <span class="input-group-text">₱</span>
                      <input 
                        type="number" 
                        v-model.number="editProduct.price" 
                        class="form-control" 
                        :class="{'is-invalid': errors.price}"
                        min="0" 
                        step="0.01" 
                        required 
                        @blur="validatePrice"
                        placeholder="0.00"
                      />
                      <div class="invalid-feedback" v-if="errors.price">{{ errors.price }}</div>
                    </div>
                  </div>
                  <div class="col-12 col-sm-6">
                    <label class="form-label fw-medium">Stock</label>
                    <div class="input-group">
                      <span class="input-group-text">
                        <i class="bi bi-box-seam"></i>
                      </span>
                      <input 
                        type="number" 
                        v-model.number="editProduct.stock" 
                        class="form-control" 
                        :class="{'is-invalid': errors.stock}"
                        min="0" 
                        required 
                        @blur="validateStock"
                        placeholder="0"
                      />
                      <div class="invalid-feedback" v-if="errors.stock">{{ errors.stock }}</div>
                    </div>
                  </div>
                </div>
                
                <div class="form-group mb-3">
                  <label class="form-label fw-medium">Product Image</label>
                  <div class="custom-file-upload">
                    <input 
                      type="file" 
                      id="productImage"
                      @change="handleEditImage" 
                      class="form-control" 
                      accept="image/*" 
                    />
                    <label for="productImage" class="file-upload-label">
                      <i class="bi bi-cloud-arrow-up me-2"></i>
                      Choose a new image
                    </label>
                  </div>
                  <small class="text-muted d-block mt-1">
                    Recommended size: 800x800px, max 2MB
                  </small>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" @click="closeEditModal">
                <i class="bi bi-x-circle me-1"></i>Cancel
              </button>
              <button type="submit" class="btn btn-primary" :disabled="isSubmitting">
                <span v-if="isSubmitting" class="spinner-border spinner-border-sm me-1" role="status"></span>
                <i v-else class="bi bi-check-circle me-1"></i>
                {{ isSubmitting ? 'Saving...' : 'Save Changes' }}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>

    <!-- Confirmation Modal -->
    <div class="modal fade confirm-modal" ref="confirmModal" tabindex="-1" aria-labelledby="confirmModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered modal-sm">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="confirmModalLabel">Confirm Deletion</h5>
            <button type="button" class="btn-close" @click="hideConfirmModal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            <i class="bi bi-exclamation-triangle-fill text-danger mb-3" style="font-size: 2rem;"></i>
            <p>Are you sure you want to delete this product? This action cannot be undone.</p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" @click="hideConfirmModal">Cancel</button>
            <button type="button" class="btn btn-danger" @click="deleteProduct" :disabled="isDeleting">
              <span v-if="isDeleting" class="spinner-border spinner-border-sm me-1" role="status"></span>
              <i v-else class="bi bi-trash me-1"></i>
              {{ isDeleting ? 'Deleting...' : 'Delete' }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, reactive, watch } from 'vue'
import axios from '../axios'
import * as bootstrap from 'bootstrap'
import { useToastStore } from '../stores/toastStore'
import { auth } from '../auth'

export default {
  setup() {
    const products = ref([])
    const currentPage = ref(1)
    const perPage = ref(5)
    const editProduct = reactive({ id: null, name: '', description: '', price: 0, stock: 0, image: null })
    const editImage = ref(null)
    const modalInstance = ref(null)
    const confirmModalInstance = ref(null)
    const orders = ref([])
    const searchQuery = ref('')
    const editModal = ref(null)
    const confirmModal = ref(null)
    const productToDelete = ref(null)
    const isLoading = ref(true)
    const isRefreshing = ref(false)
    const isSubmitting = ref(false)
    const isDeleting = ref(false)
    
    const { success, error: showError } = useToastStore()
    
    const errors = reactive({
      name: '',
      description: '',
      price: '',
      stock: ''
    })
    
    const recentOrders = computed(() => {
      return [...orders.value]
        .sort((a, b) => b.id - a.id)
        .slice(0, 5)
    })
    
    const validateName = () => {
      if (!editProduct.name.trim()) {
        errors.name = 'Product name is required'
        return false
      }
      errors.name = ''
      return true
    }
    
    const validateDescription = () => {
      if (!editProduct.description.trim()) {
        errors.description = 'Product description is required'
        return false
      }
      errors.description = ''
      return true
    }
    
    const validatePrice = () => {
      if (editProduct.price === null || editProduct.price === undefined) {
        errors.price = 'Price is required'
        return false
      }
      if (editProduct.price < 0) {
        errors.price = 'Price cannot be negative'
        return false
      }
      errors.price = ''
      return true
    }
    
    const validateStock = () => {
      if (editProduct.stock === null || editProduct.stock === undefined) {
        errors.stock = 'Stock quantity is required'
        return false
      }
      if (editProduct.stock < 0) {
        errors.stock = 'Stock cannot be negative'
        return false
      }
      errors.stock = ''
      return true
    }
    
    const validateForm = () => {
      const isNameValid = validateName()
      const isDescriptionValid = validateDescription()
      const isPriceValid = validatePrice()
      const isStockValid = validateStock()
      
      return isNameValid && isDescriptionValid && isPriceValid && isStockValid
    }
    
    const filteredProducts = computed(() => {
      let result = [...products.value]
      
      if (searchQuery.value) {
        const query = searchQuery.value.toLowerCase()
        result = result.filter(p => 
          p.name.toLowerCase().includes(query) || 
          p.description.toLowerCase().includes(query)
        )
      }
      
      return result
    })
    
    const totalPages = computed(() => {
      return Math.ceil(filteredProducts.value.length / perPage.value)
    })
    
    const paginatedProducts = computed(() => {
      const start = (currentPage.value - 1) * perPage.value
      return filteredProducts.value.slice(start, start + perPage.value)
    })
    
    const lowStockCount = computed(() => {
      return products.value.filter(p => p.stock < 5).length
    })
    
    const fullImageUrl = (path) => {
      if (!path) return ''
      return path.startsWith('http') ? path : `http://127.0.0.1:8000${path.startsWith('/') ? path : '/media/' + path}`
    }
    
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(2)
    }
    
    const truncate = (text, length) => {
      if (!text) return ''
      return text.length > length ? text.substring(0, length) + '...' : text
    }
    
    const formatDate = (dateString) => {
      const options = { year: 'numeric', month: 'short', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    }
    
    const changePage = (page) => {
      if (page >= 1 && page <= totalPages.value) {
        currentPage.value = page
        setTimeout(() => {
          const tableElement = document.querySelector('.card')
          if (tableElement) {
            tableElement.scrollIntoView({ behavior: 'smooth', block: 'start' })
          }
        }, 100)
      }
    }
    
    const getPageNumbers = () => {
      const pages = []
      const maxVisiblePages = 5
      
      if (totalPages.value <= maxVisiblePages) {
        for (let i = 1; i <= totalPages.value; i++) {
          pages.push(i)
        }
      } else {
        pages.push(1)
        
        let start = Math.max(2, currentPage.value - 1)
        let end = Math.min(totalPages.value - 1, start + 2)
        
        if (end === totalPages.value - 1) {
          start = Math.max(2, end - 2)
        }
        
        if (start > 2) {
          pages.push('...')
        }
        
        for (let i = start; i <= end; i++) {
          pages.push(i)
        }
        
        if (end < totalPages.value - 1) {
          pages.push('...')
        }
        
        pages.push(totalPages.value)
      }
      
      return pages
    }
    
    const resetSearch = () => {
      searchQuery.value = ''
      currentPage.value = 1
    }
    
    const fetchProducts = async (showLoading = true) => {
      if (showLoading) {
        isLoading.value = true
      } else {
        isRefreshing.value = true
      }
      
      try {
        console.log('📦 Fetching admin products...')
        const res = await axios.get('products/')
        products.value = res.data || []
        
        if (products.value.length === 0) {
          console.warn('No admin products received from API')
          showError('No products available at the moment.')
        } else {
          console.log('✅ Loaded', products.value.length, 'admin products')
        }
      } catch (error) {
        console.error('Error fetching admin products:', error)
        showError('Failed to load products. Please try refreshing the page.')
      } finally {
        isLoading.value = false
        isRefreshing.value = false
      }
    }
    
    const refreshProducts = async () => {
      await fetchProducts(false)
      success('Products refreshed successfully!')
    }
    
    const fetchAllOrders = async () => {
      try {
        console.log('📋 Fetching admin orders...')
        const res = await axios.get('orders/')
        orders.value = res.data || []
        
        if (orders.value.length === 0) {
          console.warn('No admin orders received from API')
        } else {
          console.log('✅ Loaded', orders.value.length, 'admin orders')
        }
      } catch (err) {
        console.error('Error fetching admin orders:', err)
        showError('Failed to load orders.')
      }
    }
    
    const openEditModal = (product) => {
      Object.keys(errors).forEach(key => {
        errors[key] = ''
      })
      
      editProduct.id = product.id
      editProduct.name = product.name
      editProduct.description = product.description
      editProduct.price = product.price
      editProduct.stock = product.stock
      editProduct.image = product.image
      
      editImage.value = null
      
      if (modalInstance.value) {
        modalInstance.value.show()
      } else if (editModal.value) {
        modalInstance.value = new bootstrap.Modal(editModal.value)
        modalInstance.value.show()
      }
    }
    
    const closeEditModal = () => {
      if (modalInstance.value) {
        modalInstance.value.hide()
      }
    }
    
    const handleEditImage = (e) => {
      editImage.value = e.target.files[0]
    }
    
    const validateAndSubmitEdit = () => {
      if (validateForm()) {
        submitEdit()
      } else {
        showError('Please fix the errors in the form')
      }
    }
    
    const submitEdit = async () => {
      isSubmitting.value = true
      
      const formData = new FormData()
      formData.append('name', editProduct.name)
      formData.append('description', editProduct.description)
      formData.append('price', editProduct.price)
      formData.append('stock', editProduct.stock)
      
      if (editImage.value) {
        formData.append('image', editImage.value)
      }

      try {
        await axios.patch(`products/${editProduct.id}/`, formData, {
          headers: { 'Content-Type': 'multipart/form-data' }
        })
        success('Product updated successfully!')
        fetchProducts()
        closeEditModal()
      } catch (err) {
        console.error('Error updating product:', err)
        showError('Failed to update product.')
        
        if (err.response && err.response.data) {
          const apiErrors = err.response.data
          Object.keys(apiErrors).forEach(key => {
            if (key in errors) {
              errors[key] = apiErrors[key][0]
            }
          })
        }
      } finally {
        isSubmitting.value = false
      }
    }
    
    const confirmDeleteProduct = (id) => {
      productToDelete.value = id
      if (confirmModalInstance.value) {
        confirmModalInstance.value.show()
      } else if (confirmModal.value) {
        confirmModalInstance.value = new bootstrap.Modal(confirmModal.value)
        confirmModalInstance.value.show()
      }
    }
    
    const hideConfirmModal = () => {
      if (confirmModalInstance.value) {
        confirmModalInstance.value.hide()
      }
    }
    
    const deleteProduct = async () => {
      if (!productToDelete.value) return
      
      isDeleting.value = true
      
      try {
        await axios.delete(`products/${productToDelete.value}/`)
        success('Product deleted successfully.')
        fetchProducts()
        hideConfirmModal()
      } catch (err) {
        console.error('Error deleting product:', err)
        
        if (err.response && err.response.data && err.response.data.error) {
          showError(err.response.data.error)
        } else {
          showError('Cannot delete product. It may be in an order.')
        }
      } finally {
        isDeleting.value = false
      }
    }
    
    onMounted(async () => {
      console.log('🚀 MonitorProducts component mounted')
      
      if (editModal.value) {
        modalInstance.value = new bootstrap.Modal(editModal.value)
      }
      
      if (confirmModal.value) {
        confirmModalInstance.value = new bootstrap.Modal(confirmModal.value)
      }
      
      const token = localStorage.getItem('access')
      const isStaff = localStorage.getItem('is_staff') === 'true'
      if (!token || !isStaff) {
        showError('Access denied. Staff only.')
        window.location.href = '/login'
        return
      }
      
      // Wait for auth initialization
      await auth.waitForInitialization()
      
      // Fetch data
      await Promise.all([fetchProducts(), fetchAllOrders()])
    })

    watch(() => auth.isLoggedIn, async (newValue) => {
      if (newValue && auth.isInitialized && auth.isStaff) {
        await Promise.all([fetchProducts(), fetchAllOrders()])
      }
    })
    
    return {
      products,
      filteredProducts,
      paginatedProducts,
      currentPage,
      totalPages,
      editProduct,
      editModal,
      confirmModal,
      orders,
      recentOrders,
      searchQuery,
      lowStockCount,
      isLoading,
      isRefreshing,
      isSubmitting,
      isDeleting,
      errors,
      fullImageUrl,
      formatPrice,
      truncate,
      formatDate,
      changePage,
      getPageNumbers,
      resetSearch,
      refreshProducts,
      openEditModal,
      closeEditModal,
      handleEditImage,
      validateAndSubmitEdit,
      confirmDeleteProduct,
      hideConfirmModal,
      deleteProduct,
      validateName,
      validateDescription,
      validatePrice,
      validateStock
    }
  }
}
</script>

<style scoped>
/* CSS Variables */
:root {
  --primary: #006884;
  --primary-dark: #053D57;
  --primary-light: #97BCC7;
  --light: #F2F1EF;
  --accent: #97BCC7;
  --text-dark: #2D3748;
  --text-light: #718096;
  --success: #48BB78;
  --warning: #F6AD55;
  --danger: #F56565;
  --background: #F7FAFC;
  --card-bg: #FFFFFF;
