import axios from "axios"

// Import mock services
import mockApiService from "./services/mockApiService"
import adminMockApiService from "./services/adminMockApiService"

// Check if we're in demo mode
const isDemoMode =
  window.location.hostname.includes("github.io") ||
  process.env.VUE_APP_DEMO_MODE === "true" ||
  !process.env.VUE_APP_API_URL

console.log("🔐 Auth setup - Demo mode:", isDemoMode)

class Auth {
  constructor() {
    this.isLoggedIn = false
    this.isStaff = false
    this.isInitialized = false
    this.initializationPromise = null

    // Initialize immediately
    this.initialize()
  }

  async initialize() {
    if (this.initializationPromise) {
      return this.initializationPromise
    }

    this.initializationPromise = this._performInitialization()
    return this.initializationPromise
  }

  async _performInitialization() {
    console.log("🔄 Initializing auth...")

    const token = localStorage.getItem("access")
    const isStaff = localStorage.getItem("is_staff") === "true"
    const username = localStorage.getItem("username")

    if (token) {
      console.log("🔑 Found existing token, staff:", isStaff, "username:", username)

      if (isDemoMode) {
        // In demo mode, trust the stored values
        this.isLoggedIn = true
        this.isStaff = isStaff
        this.isInitialized = true

        console.log("✅ Auth initialized (demo mode) - Staff:", this.isStaff)

        // Ensure appropriate mock data is initialized
        if (this.isStaff) {
          console.log("🔧 Initializing admin mock data...")
          const { ensureAdminMockDataExists } = await import("./data/adminMockData")
          ensureAdminMockDataExists()
        } else {
          console.log("🔧 Initializing customer mock data...")
          const { initializeMockData } = await import("./data/mockData")
          initializeMockData()
        }

        // Dispatch auth change event
        this._dispatchAuthChange()
        return
      }

      // Real API token validation
      try {
        const response = await axios.get(`${process.env.VUE_APP_API_URL}user/`, {
          headers: { Authorization: `Bearer ${token}` },
        })

        this.isLoggedIn = true
        this.isStaff = response.data.is_staff || false
        localStorage.setItem("is_staff", this.isStaff.toString())
        localStorage.setItem("username", response.data.username)

        console.log("✅ Auth initialized (real API) - Staff:", this.isStaff)
      } catch (error) {
        console.error("❌ Token validation failed:", error)
        this.logout()
      }
    } else {
      console.log("ℹ️ No token found")
      // Ensure we're in logged out state
      this.isLoggedIn = false
      this.isStaff = false
    }

    this.isInitialized = true
    this._dispatchAuthChange()
  }

  async login(username, password) {
    console.log("🔐 Attempting login for:", username)

    try {
      let response

      if (isDemoMode) {
        // Use appropriate mock service based on username
        const isAdminLogin = username === "admin"
        const mockService = isAdminLogin ? adminMockApiService : mockApiService

        console.log("🎭 Using", isAdminLogin ? "admin" : "customer", "mock service for login")

        response = await mockService.post("token/", { username, password })

        // Set staff status based on username
        this.isStaff = isAdminLogin
        localStorage.setItem("is_staff", this.isStaff.toString())
        localStorage.setItem("username", username)
        localStorage.setItem("user_id", isAdminLogin ? "1" : "2")

        console.log("✅ Mock login successful - Staff:", this.isStaff)

        // Initialize appropriate mock data
        if (this.isStaff) {
          console.log("🔧 Initializing admin mock data after login...")
          const { ensureAdminMockDataExists } = await import("./data/adminMockData")
          ensureAdminMockDataExists()
        } else {
          console.log("🔧 Initializing customer mock data after login...")
          const { initializeMockData } = await import("./data/mockData")
          initializeMockData()
        }
      } else {
        // Real API login
        response = await axios.post(`${process.env.VUE_APP_API_URL}token/`, {
          username,
          password,
        })

        // Get user details to determine staff status
        const userResponse = await axios.get(`${process.env.VUE_APP_API_URL}user/`, {
          headers: { Authorization: `Bearer ${response.data.access}` },
        })

        this.isStaff = userResponse.data.is_staff || false
        localStorage.setItem("is_staff", this.isStaff.toString())
        localStorage.setItem("username", username)
        localStorage.setItem("user_id", userResponse.data.id.toString())

        console.log("✅ Real API login successful - Staff:", this.isStaff)
      }

      // Store tokens
      localStorage.setItem("access", response.data.access)
      localStorage.setItem("refresh", response.data.refresh)

      this.isLoggedIn = true
      this.isInitialized = true

      // Dispatch auth change event
      this._dispatchAuthChange()

      return response.data
    } catch (error) {
      console.error("❌ Login failed:", error)
      throw error
    }
  }

  logout() {
    console.log("🚪 Logging out...")

    localStorage.removeItem("access")
    localStorage.removeItem("refresh")
    localStorage.removeItem("is_staff")
    localStorage.removeItem("username")
    localStorage.removeItem("user_id")

    this.isLoggedIn = false
    this.isStaff = false
    this.isInitialized = true

    // Dispatch auth change event
    this._dispatchAuthChange()
  }

  async checkTokenValidity() {
    const token = localStorage.getItem("access")

    if (!token) {
      this.logout()
      return false
    }

    if (isDemoMode) {
      // In demo mode, tokens are always valid
      return true
    }

    try {
      await axios.get(`${process.env.VUE_APP_API_URL}user/`, {
        headers: { Authorization: `Bearer ${token}` },
      })
      return true
    } catch (error) {
      console.error("Token validation failed:", error)
      this.logout()
      return false
    }
  }

  _dispatchAuthChange() {
    console.log("📢 Dispatching auth change events - Logged in:", this.isLoggedIn, "Staff:", this.isStaff)

    const eventData = {
      isLoggedIn: this.isLoggedIn,
      isStaff: this.isStaff,
      isInitialized: this.isInitialized,
      timestamp: Date.now(),
    }

    // Dispatch multiple event types to ensure compatibility
    window.dispatchEvent(new CustomEvent("auth-changed", { detail: eventData }))
    window.dispatchEvent(new CustomEvent("auth-state-changed", { detail: eventData }))

    if (this.isLoggedIn) {
      window.dispatchEvent(new CustomEvent("login-success", { detail: eventData }))
    } else {
      window.dispatchEvent(new CustomEvent("logout-success", { detail: eventData }))
    }

    console.log("✅ Auth events dispatched successfully")
  }

  // Helper method for components to wait for initialization
  async waitForInitialization() {
    if (this.isInitialized) {
      return Promise.resolve()
    }

    if (this.initializationPromise) {
      return this.initializationPromise
    }

    return this.initialize()
  }
}

// Create and export singleton instance
export const auth = new Auth()
export default auth
