  margin-bottom: 2.5rem;
}

.section-title {
  font-size: clamp(1.5rem, 3.5vw, 2.25rem);
  font-weight: 700;
  color: #2D3748;
  margin-bottom: 0.6rem;
}

.section-subtitle {
  font-size: clamp(0.85rem, 1.8vw, 1rem);
  color: #718096;
  max-width: 600px;
  margin: 0 auto;
}

/* Feature Cards */
.feature-card {
  background: white;
  border-radius: 16px;
  padding: 1.75rem 1.25rem;
  text-align: center;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 104, 132, 0.1);
  transition: all 0.3s ease;
  height: 100%;
  margin-bottom: 1.25rem;
}

.feature-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.1);
  border-color: #006884;
}

.feature-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #006884 0%, #053D57 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1rem;
  font-size: 1.5rem;
  color: white;
}

.feature-card h3 {
  font-size: clamp(1.1rem, 2.5vw, 1.3rem);
  font-weight: 700;
  color: #2D3748;
  margin-bottom: 0.6rem;
}

.feature-card p {
  color: #718096;
  line-height: 1.6;
  margin-bottom: 1rem;
  font-size: 0.85rem;
}

.feature-highlight {
  display: inline-flex;
  align-items: center;
  background: rgba(0, 104, 132, 0.1);
  color: #006884;
  padding: 0.3rem 0.6rem;
  border-radius: 50px;
  font-size: 0.75rem;
  font-weight: 600;
}

/* Stats */
.stat-item {
  text-align: center;
  padding: 0.6rem;
}

.stat-number {
  font-size: clamp(1.5rem, 3.5vw, 2.25rem);
  font-weight: 800;
  color: white;
  margin-bottom: 0.2rem;
  display: block;
}

.stat-label {
  font-size: clamp(0.7rem, 1.8vw, 0.9rem);
  opacity: 0.9;
  font-weight: 500;
}

/* Category Cards */
.category-card {
  background: white;
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  height: 100%;
  cursor: pointer;
  margin-bottom: 1.25rem;
}

.category-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.1);
}

.category-image {
  height: 160px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.category-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.category-card:hover .category-image img {
  transform: scale(1.05);
}

.category-content {
  padding: 1.25rem;
}

.category-content h4 {
  font-size: clamp(1.1rem, 2.5vw, 1.3rem);
  font-weight: 700;
  color: #2D3748;
  margin-bottom: 0.4rem;
