  margin-bottom: 0.6rem;
  font-size: 0.85rem;
}

.category-stats {
  color: #006884;
  font-weight: 600;
  font-size: 0.75rem;
}

/* Testimonials */
.testimonial-card {
  background: white;
  border-radius: 14px;
  padding: 1.5rem;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 104, 132, 0.1);
  transition: all 0.3s ease;
