          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarContent" ref="navbarCollapse">
          <ul class="navbar-nav me-auto mb-2 mb-lg-0">
            <!-- STAFF NAVIGATION -->
            <li class="nav-item" v-if="auth.isLoggedIn && auth.isStaff">
              <router-link class="nav-link" to="/monitor-products" @click="closeNavbar">
                <i class="bi bi-box-seam me-1"></i> Products
              </router-link>
            </li>
            <li class="nav-item" v-if="auth.isLoggedIn && auth.isStaff">
              <router-link class="nav-link" to="/add-product" @click="closeNavbar">
                <i class="bi bi-plus-circle me-1"></i> Add Product
              </router-link>
            </li>
            <li class="nav-item" v-if="auth.isLoggedIn && auth.isStaff">
              <router-link class="nav-link" to="/monitor" @click="closeNavbar">
                <i class="bi bi-clipboard-check me-1"></i> Orders
              </router-link>
            </li>

            <!-- CUSTOMER NAVIGATION -->
            <li class="nav-item" v-if="auth.isLoggedIn && !auth.isStaff">
              <router-link class="nav-link" to="/products" @click="closeNavbar">
                <i class="bi bi-shop me-1"></i> Shop
              </router-link>
            </li>
            <li class="nav-item" v-if="auth.isLoggedIn && !auth.isStaff">
              <router-link class="nav-link position-relative" to="/cart" @click="closeNavbar">
                <i class="bi bi-cart me-1"></i> Cart
                <span v-if="cartCount > 0" class="cart-badge">{{ cartCount }}</span>
              </router-link>
            </li>
            <li class="nav-item" v-if="auth.isLoggedIn && !auth.isStaff">
              <router-link class="nav-link" to="/orders" @click="closeNavbar">
                <i class="bi bi-clock-history me-1"></i> Orders
              </router-link>
            </li>
          </ul>

          <ul class="navbar-nav ms-auto">
            <!-- Show Login only if LOGGED OUT -->
            <li class="nav-item" v-if="!auth.isLoggedIn">
              <button 
                class="nav-link btn btn-link" 
                @click="handleLoginClick"
              >
                <i class="bi bi-box-arrow-in-right me-1"></i> Login
              </button>
            </li>

            <!-- Show User Info and Logout if LOGGED IN -->
            <li class="nav-item user-nav-item" v-if="auth.isLoggedIn">
              <div class="user-nav-container d-flex align-items-center">
                <!-- User Info -->
                <div class="user-info d-flex align-items-center me-2">
                  <i class="bi bi-person-circle me-1 user-icon"></i>
                  <div class="user-details">
                    <span class="username">{{ username || 'User' }}</span>
                    <small class="user-role d-block">{{ auth.isStaff ? 'Admin' : 'Customer' }}</small>
                  </div>
                </div>
                
                <!-- Logout Button -->
                <button 
                  class="btn btn-logout d-flex align-items-center" 
                  @click="logout"
                  title="Logout"
                >
                  <i class="bi bi-box-arrow-right me-1"></i>
                  <span class="logout-text">Logout</span>
                </button>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </nav>

    <main class="main-content">
      <router-view v-slot="{ Component }">
        <transition name="fade" mode="out-in">
          <component :is="Component" :key="$route.fullPath" />
        </transition>
      </router-view>
    </main>

    <footer class="footer mt-auto py-3">
      <div class="container">
        <div class="row">
          <div class="col-md-4 mb-3 mb-md-0">
            <h5 class="footer-heading">Bluora</h5>
            <p class="footer-text">Your one-stop shop for all your needs. Quality products at affordable prices.</p>
          </div>
          <div class="col-md-4 mb-3 mb-md-0">
            <h5 class="footer-heading">Quick Links</h5>
            <ul class="footer-links">
              <li><a href="#" @click.prevent="handleShopClick">Products</a></li>
              <li><a href="#" @click.prevent="handleCartClick">Cart</a></li>
              <li><a href="#" @click.prevent="handleOrdersClick">Orders</a></li>
            </ul>
          </div>
          <div class="col-md-4">
            <h5 class="footer-heading">Contact Us</h5>
            <p class="footer-text">
              <i class="bi bi-envelope me-2"></i> support@bluora.com<br>
              <i class="bi bi-telephone me-2"></i> +1 (123) 456-7890<br>
              <i class="bi bi-geo-alt me-2"></i> 123 E-Commerce St, Digital City
            </p>
          </div>
        </div>
