          <p class="mb-0">© 2025 Bluora. All rights reserved.</p>
          <div class="social-icons">
            <a href="#" class="social-icon"><i class="bi bi-facebook"></i></a>
            <a href="#" class="social-icon"><i class="bi bi-twitter"></i></a>
            <a href="#" class="social-icon"><i class="bi bi-instagram"></i></a>
          </div>
        </div>
      </div>
    </footer>
    
    <!-- Toast Notification Component -->
    <ToastNotification />
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted, watch, nextTick } from 'vue'
import { auth } from './auth'
import axios from './axios'
import { useRouter } from 'vue-router'
import 'bootstrap/dist/js/bootstrap.bundle.min.js'
import 'bootstrap-icons/font/bootstrap-icons.css'
import ToastNotification from './components/ToastNotification.vue'
import * as bootstrap from 'bootstrap'

export default {
  components: {
    ToastNotification
  },
  setup() {
  const router = useRouter()
  const cartCount = ref(0)
  const username = ref('')
  const navbarCollapse = ref(null)
  let bsNavbar = null
  const isNavbarOpen = ref(false)
  
  // Create reactive references that will force re-renders
  const authState = ref({
    isLoggedIn: false,
    isStaff: false,
    isInitialized: false
  })
  
  const updateCartCount = () => {
    const cart = JSON.parse(localStorage.getItem('cart')) || []
    cartCount.value = cart.length
  }
  
  // Force update auth state from auth object
  const forceUpdateAuthState = () => {
    console.log('🔄 Force updating auth state:', {
      isLoggedIn: auth.isLoggedIn,
      isStaff: auth.isStaff,
      isInitialized: auth.isInitialized
    })
    
    authState.value = {
      isLoggedIn: auth.isLoggedIn,
      isStaff: auth.isStaff,
      isInitialized: auth.isInitialized
    }
    
    // Also update username
    fetchUsername()
  }
  
  // Authentication check helper
  const requireAuth = (targetRoute) => {
    if (!auth.isLoggedIn) {
      router.push('/login')
      return false
    }
    router.push(targetRoute)
    return true
  }
  
  // Navigation handlers
  const handleLoginClick = () => {
    closeNavbar()
    router.push('/login')
  }
  
  const handleRegisterClick = () => {
    closeNavbar()
    router.push('/login') // Redirect to login instead of register for demo
  }
  
  const handleShopClick = () => {
    requireAuth('/products')
  }
  
  const handleCartClick = () => {
    requireAuth('/cart')
  }
  
  const handleOrdersClick = () => {
    requireAuth('/orders')
  }
  
  const logout = () => {
    auth.logout()
    forceUpdateAuthState() // Force update immediately
    closeNavbar()
    
    // Check if we're on GitHub Pages
    if (window.location.hostname.includes('github.io')) {
      // Full redirect needed for GitHub Pages
      window.location.href = 'https://lorendale.github.io/vue-final-project/#/'
    } else {
      // Normal navigation for local development
      router.push('/')
    }
  }
  
  // Toggle navbar open/close
  const toggleNavbar = () => {
    if (bsNavbar) {
      if (isNavbarOpen.value) {
        bsNavbar.hide()
      } else {
        bsNavbar.show()
      }
      isNavbarOpen.value = !isNavbarOpen.value
    }
  }
  
  // Close navbar when a link is clicked (mobile view)
  const closeNavbar = () => {
    if (bsNavbar && isNavbarOpen.value) {
      bsNavbar.hide()
      isNavbarOpen.value = false
    }
  }
  
  // Custom event bus for cart updates
  const handleStorageChange = (e) => {
    if (e.key === 'cart') {
      updateCartCount()
    }
  }
  
  // Create a custom event for cart updates
  const handleCartUpdate = () => {
    updateCartCount()
  }
  
  // Handle auth state changes - multiple event types
  const handleAuthChange = async (event) => {
    console.log('📢 Auth change event received:', event.type, event.detail)
    await nextTick()
    forceUpdateAuthState()
  }
  
  const fetchUsername = async () => {
    if (auth.isLoggedIn) {
      try {
        // Try to get from localStorage first
        const storedUsername = localStorage.getItem('username')
        if (storedUsername) {
          username.value = storedUsername
          return
        }
        
        // Fallback to API call
        const response = await axios.get('user/')
        username.value = response.data.username
        localStorage.setItem('username', response.data.username)
      } catch (error) {
        console.error('Failed to fetch username:', error)
        // Set default username based on role
        username.value = auth.isStaff ? 'admin' : 'user'
      }
    } else {
      username.value = ''
    }
  }
  
  // Periodic auth state check (fallback)
  const startAuthStateWatcher = () => {
    setInterval(() => {
      const currentState = {
        isLoggedIn: auth.isLoggedIn,
        isStaff: auth.isStaff,
        isInitialized: auth.isInitialized
      }
      
      if (JSON.stringify(currentState) !== JSON.stringify(authState.value)) {
        console.log('🔄 Auth state drift detected, updating...')
        forceUpdateAuthState()
      }
    }, 1000) // Check every second
  }
  
  onMounted(async () => {
    console.log('🚀 App.vue mounted, initializing...')
    
    // Wait for auth initialization
    await auth.waitForInitialization()
    
    // Force update auth state after initialization
    forceUpdateAuthState()
    
    // Check token validity on page load
    if (auth.isLoggedIn) {
      await auth.checkTokenValidity()
      forceUpdateAuthState() // Update again after token check
    }
    
    // Initialize cart counts
    updateCartCount()
    
    // Listen for localStorage changes (works across tabs)
    window.addEventListener('storage', handleStorageChange)
    
    // Listen for custom cart update events
    window.addEventListener('cart-updated', handleCartUpdate)
    
    // Listen for ALL possible auth state change events
    window.addEventListener('auth-changed', handleAuthChange)
    window.addEventListener('auth-state-changed', handleAuthChange)
    window.addEventListener('login-success', handleAuthChange)
    window.addEventListener('logout-success', handleAuthChange)
    
    // Start periodic auth state watcher as fallback
    startAuthStateWatcher()
    
    // Initialize Bootstrap navbar collapse
    const navbarElement = document.getElementById('navbarContent')
    if (navbarElement) {
      bsNavbar = new bootstrap.Collapse(navbarElement, {
        toggle: false
      })
      
      // Listen for Bootstrap events to update our state
      navbarElement.addEventListener('shown.bs.collapse', () => {
        isNavbarOpen.value = true
      })
      
      navbarElement.addEventListener('hidden.bs.collapse', () => {
        isNavbarOpen.value = false
      })
      
      // Add event listener to close navbar when clicking outside
      document.addEventListener('click', (event) => {
        if (!isNavbarOpen.value) return
        
        const isToggleButton = event.target.closest('.navbar-toggler')
        const isInsideNavbar = event.target.closest('.navbar-collapse')
        
        if (!isToggleButton && !isInsideNavbar) {
          closeNavbar()
        }
      })
    }
    
    // Close navbar when route changes
    if (window.history) {
      window.addEventListener('popstate', closeNavbar)
    }
    
    // Close navbar when window resizes to desktop size
    window.addEventListener('resize', () => {
      if (window.innerWidth >= 992 && isNavbarOpen.value) {
        closeNavbar()
      }
    })
    
    console.log('✅ App.vue initialization complete')
  })
  
  onUnmounted(() => {
    window.removeEventListener('storage', handleStorageChange)
    window.removeEventListener('cart-updated', handleCartUpdate)
    window.removeEventListener('auth-changed', handleAuthChange)
    window.removeEventListener('auth-state-changed', handleAuthChange)
    window.removeEventListener('login-success', handleAuthChange)
    window.removeEventListener('logout-success', handleAuthChange)
    window.removeEventListener('popstate', closeNavbar)
    window.removeEventListener('resize', () => {})
    
    // Remove Bootstrap event listeners
    const navbarElement = document.getElementById('navbarContent')
    if (navbarElement) {
      navbarElement.removeEventListener('shown.bs.collapse', () => {})
      navbarElement.removeEventListener('hidden.bs.collapse', () => {})
    }
    
    // Remove click event listener
    document.removeEventListener('click', () => {})
  })
  
  // Watch for changes to localStorage cart
  watch(() => localStorage.getItem('cart'), () => {
    updateCartCount()
  })
  
  // Watch for route changes and update auth state
  watch(() => router.currentRoute.value.path, () => {
    forceUpdateAuthState()
  })
  
  return {
    // Use reactive authState instead of auth object directly
    auth: authState,
    cartCount,
    username,
    logout,
    updateCartCount,
    closeNavbar,
    toggleNavbar,
    navbarCollapse,
    handleLoginClick,
    handleRegisterClick,
    handleShopClick,
    handleCartClick,
    handleOrdersClick
  }
}
}
</script>

