import axios from "axios"
import { auth } from "./auth"

// Import both mock services
import mockApiService from "./services/mockApiService"
import adminMockApiService from "./services/adminMockApiService"

// Check if we're in demo mode (GitHub Pages or local demo)
const isDemoMode =
  window.location.hostname.includes("github.io") ||
  process.env.VUE_APP_DEMO_MODE === "true" ||
  !process.env.VUE_APP_API_URL

console.log("🔧 Axios setup - Demo mode:", isDemoMode)

let axiosInstance

if (isDemoMode) {
  console.log("🎭 Using mock API services for demo mode")

  // Create a mock axios instance that routes to appropriate mock service
  axiosInstance = {
    get: async (url, config = {}) => {
      console.log("📡 Mock GET request:", url)

      // Check if user is staff to determine which mock service to use
      const isStaff = localStorage.getItem("is_staff") === "true"
      const mockService = isStaff ? adminMockApiService : mockApiService

      console.log("👤 Using", isStaff ? "admin" : "customer", "mock service for:", url)

      try {
        const response = await mockService.get(url, config.data, config)
        console.log("✅ Mock response received:", response.data?.length || "data")
        return response
      } catch (error) {
        console.error("❌ Mock API error:", error)
        throw error
      }
    },

    post: async (url, data, config = {}) => {
      console.log("📡 Mock POST request:", url)

      const isStaff = localStorage.getItem("is_staff") === "true"
      const mockService = isStaff ? adminMockApiService : mockApiService

      console.log("👤 Using", isStaff ? "admin" : "customer", "mock service for:", url)

      try {
        const response = await mockService.post(url, data, config)
        console.log("✅ Mock POST response received")
        return response
      } catch (error) {
        console.error("❌ Mock API POST error:", error)
        throw error
      }
    },

    put: async (url, data, config = {}) => {
      console.log("📡 Mock PUT request:", url)

      const isStaff = localStorage.getItem("is_staff") === "true"
      const mockService = isStaff ? adminMockApiService : mockApiService

      return await mockService.put(url, data, config)
    },

    patch: async (url, data, config = {}) => {
      console.log("📡 Mock PATCH request:", url)

      const isStaff = localStorage.getItem("is_staff") === "true"
      const mockService = isStaff ? adminMockApiService : mockApiService

      return await mockService.patch(url, data, config)
    },

    delete: async (url, config = {}) => {
      console.log("📡 Mock DELETE request:", url)

      const isStaff = localStorage.getItem("is_staff") === "true"
      const mockService = isStaff ? adminMockApiService : mockApiService

      return await mockService.delete(url, config)
    },

    // Add interceptors property for compatibility
    interceptors: {
      request: { use: () => {} },
      response: { use: () => {} },
    },
  }
} else {
  // Real API configuration
  console.log("🌐 Using real API service")

  axiosInstance = axios.create({
    baseURL: process.env.VUE_APP_API_URL || "http://127.0.0.1:8000/api/",
    timeout: 10000,
    headers: {
      "Content-Type": "application/json",
    },
  })

  // Request interceptor to add auth token
  axiosInstance.interceptors.request.use(
    (config) => {
      const token = localStorage.getItem("access")
      if (token) {
        config.headers.Authorization = `Bearer ${token}`
      }
      return config
    },
    (error) => {
      return Promise.reject(error)
    },
  )

  // Response interceptor to handle token refresh
  axiosInstance.interceptors.response.use(
    (response) => response,
    async (error) => {
      const originalRequest = error.config

      if (error.response?.status === 401 && !originalRequest._retry) {
        originalRequest._retry = true

        try {
          const refreshToken = localStorage.getItem("refresh")
          if (refreshToken) {
            const response = await axios.post(`${process.env.VUE_APP_API_URL}token/refresh/`, {
              refresh: refreshToken,
            })

            const newToken = response.data.access
            localStorage.setItem("access", newToken)
            originalRequest.headers.Authorization = `Bearer ${newToken}`

            return axiosInstance(originalRequest)
          }
        } catch (refreshError) {
          console.error("Token refresh failed:", refreshError)
          auth.logout()
          window.location.href = "/login"
        }
      }

      return Promise.reject(error)
    },
  )
}

export default axiosInstance
